import {search} from '@/network/request.js'
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
export default{
	components: {uniSearchBar},
	methods:{
	search(e){
		let {value} = e
		uni.navigateTo({
		    url: `/pages/searchList/index?keywords=${value}`
		});
	}	
	}
}
